html {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    background: #fff;
    color: #000;
    letter-spacing: normal;



    @media screen and (max-width: $w1440) {
        font-size: 15px;
    }

    @media screen and (max-width: $w1280) {
        font-size: 14px;
    }
}


button:active,
button:hover,
input:active,
input:focus,
input:hover {
    outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
    line-height: 130%;
}

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
    color: #000;
    text-decoration: none;
}

img {
    display: block;
    max-width: 100%;
}

ul {
    list-style-type: none;
}

.hidden {
    overflow: hidden;

    @media screen and (min-width: $w1024) {
        margin-right: 17px;
    }
}

.hide {
    display: none;
}

.rotate {
    transform: rotate(180deg);
}

section {
    overflow: hidden;
    position: relative;

}

main {
    width: 100%;
}

.container {
    max-width: 1720px;
    width: 100%;
    margin: 0 auto;
    padding: 0 rem(30);
    box-sizing: border-box;

    @media screen and (max-width: $w1440) {
        max-width: 1300px;
    }

    @media screen and (max-width: $w1376) {
        max-width: 1200px;
    }

    @media screen and (max-width: $w1280) {
        padding: 0 rem(30);
    }

    @media screen and (max-width: $w1024) {
        padding: 0 rem(25);
    }

    @media screen and (max-width: $w580) {
        padding: 0 rem(15);
    }
}

.pull-right {
    justify-content: flex-end !important;
}

.pull-left {
    justify-content: flex-start !important;
}

::-webkit-input-placeholder {
    /* Chrome */
    color: $main;
}

:-ms-input-placeholder {
    /* IE 10+ */
    color: $main;
}

::-moz-placeholder {
    /* Firefox 19+ */
    color: $main;
    opacity: 1;
}

:-moz-placeholder {
    /* Firefox 4 - 18 */
    color: $main;
    opacity: 1;
}

.gmnoprint {
    opacity: 0;
}

.gm-style-cc {
    opacity: 0;
}

.fancybox__thumbs .carousel__slide .fancybox__thumb::after {
    border: 2px solid var(--fancybox-accent-color);
}



:root {
    --fancybox-accent-color: #9C938C;
    --carousel-button-width: 48px;
    --carousel-button-height: 48px;
    --carousel-button-svg-width: 27px;
    --carousel-button-svg-height: 27px;
    --fancybox-bg: rgba(24, 24, 27, 0.92);
    --fancybox-thumbs-width: 96px;
    --fancybox-thumbs-border-radius: 0px;
    --swiper-navigation-color: #fff;

    @media screen and (max-width: $w1440) {
        --swiper-navigation-size: 28px
    }
}

.fancybox__nav {
    @media screen and (max-width: $w580) {
        display: none !important;
    }
}


/* ================== */

@mixin section-title {
    font-style: normal;
    font-weight: normal;
    font-size: rem(48);
    line-height: 110%;
    color: $black;

    @media screen and (max-width: $w1280) {
        font-size: rem(38);
    }

    @media screen and (max-width: $w1024) {
        font-size: rem(36);
    }

    @media screen and (max-width: $w480) {
        font-size: rem(30);
    }
}

@mixin section-subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: rem(36);
    line-height: 110%;
    color: $black;

    @media screen and (max-width: $w1280) {
        font-size: rem(30);
    }

    @media screen and (max-width: $w1024) {
        font-size: rem(24);
    }
}


@mixin section-padding {
    padding: rem(50) 0;

    @media screen and (max-width: $w580) {
        padding: rem(30) 0;
    }
}